body{margin:0;padding:0;font-family:-apple-system,BlinkMacSystemFont,'Segoe UI','Roboto','Oxygen','Ubuntu','Cantarell','Fira Sans','Droid Sans','Helvetica Neue',sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,'Courier New',monospace}
.main{display:flex;flex-direction:column;height:100vh}.main-spinner .bp3-non-ideal-state-visual{margin-top:20px}
.controller{flex:1 1 auto;margin-top:20px;margin-bottom:20px;display:flex;align-items:stretch;}.controller .center-icon-container{flex:.75 1 auto;display:flex;align-items:center;justify-content:center}.controller .slider-container{flex:.25 1 auto;display:flex;justify-content:center}.controller .slider{height:auto}
